import React, { useEffect, useRef, useState } from 'react'
import { MapContainer, GeoJSON, useMap, TileLayer } from 'react-leaflet'
import * as L from "leaflet";
import "leaflet/dist/leaflet.css"
import 'leaflet-fullscreen/dist/Leaflet.fullscreen.js';
import 'leaflet-fullscreen/dist/leaflet.fullscreen.css';
import { maxBounds, setDragging, setInitialFloodRiskMapZoom, setInitialMapZoom } from '../helpers/functions';
import subbasin_norm_flood_risk_data from "../assets/data/subbasin_norm_flood_risk_data.json"
import IndiaSubBasins from "../assets/data/IndiaSubBasins.json"
import india_boundary from "../assets/data/india_boundary.json"
import FloodRiskLegend from '../components/FloodRiskLegend';

const FloodRiskPage = () => {

    function DistrictOnEachfeature(feature, layer, selectedIndex) {
        layer.on('mouseover', function (e) {
            const DataItem = subbasin_norm_flood_risk_data.find(item => item.ID === feature.properties.ID);
            if (feature.properties && feature.properties.ID) {
                const popupContent = `
          <div>
           ID: ${feature.properties.ID}<br/>
            Sub-basin: ${feature.properties.Name}<br/>
            Normalized ${selectedIndex}: ${DataItem ? DataItem[selectedIndex] : null}
          </div>
        `;
                layer.bindTooltip(popupContent, { sticky: true });
            }
            layer.openTooltip();
        });

        layer.on('mouseout', function () {
            layer.closeTooltip();
        });
    }



    const DistrictDensity = (density => {
        return density > 0.9 ? '#860128'
            : density > 0.8 ? '#C6022D'
                : density > 0.7 ? '#ED1A28'
                    : density > 0.6 ? '#FF4E36'
                        : density > 0.4 ? '#FF8D48'
                            : density > 0.3 ? '#FFB258'
                                : density > 0.2 ? '#FFD97E'
                                    : density > 0.1 ? '#FFEDA5'
                                        : density > 0 ? '#FFFFCF'
                                            : 'white';
    })




    const DistrictStyle = (feature, parameter) => {
        const getDensityFromSummerData = (id) => {
            const DataItem = subbasin_norm_flood_risk_data.find(item => item.ID === id);
            return DataItem ? DataItem[parameter] : null;
        };

        const density = getDensityFromSummerData(feature.properties.ID);
        return ({
            fillColor: DistrictDensity(density),
            weight: 1,
            opacity: 1,
            color: 'black',
            dashArray: '2',
            fillOpacity: 1
        });
    };





    return (
        <div className='dasboard_page_container'>
            <div className='flood_risk_maps_container' style={{ height: "100%" }}>


                <div className='flood_risk_maps'>

                    <MapContainer
                        fullscreenControl={true}
                        center={[23, 84]}
                        style={{ width: '100%', height: "100%", backgroundColor: 'white', border: 'none', margin: 'auto' }}
                        zoom={setInitialFloodRiskMapZoom()}
                        maxBounds={maxBounds}
                        minZoom={setInitialFloodRiskMapZoom()}
                        keyboard={false}
                        dragging={setDragging()}
                        doubleClickZoom={false}
                    >
                        <TileLayer
                            attribution="Tiles &copy; Esri"
                            url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer/tile/{z}/{y}/{x}"
                        />

                        <div className='fllod_risk_map_heading'>
                            <p> Vulnerability </p>
                        </div>

                        <div className="flood-legend-panel-desktop">
                            <FloodRiskLegend heading="Normalized Vulnerability Index" />
                        </div>

                        <GeoJSON data={india_boundary}
                            interactive={false}
                            style={
                                {
                                    fillColor: "white",
                                    color: "black",
                                    fillOpacity: "1",
                                    weight: "1"
                                }
                            }
                        />
                        <GeoJSON data={IndiaSubBasins.features}
                            style={(feature) => DistrictStyle(feature, "Vulnerability")}
                            onEachFeature={(feature, layer) => DistrictOnEachfeature(feature, layer, "Vulnerability")}
                        />

                    </MapContainer>
                </div>

                <div className='flood_risk_maps'>
                    <MapContainer
                        fullscreenControl={true}
                        center={[23, 84]}
                        style={{ width: '100%', height: "100%", backgroundColor: 'white', border: 'none', margin: 'auto' }}
                        zoom={setInitialFloodRiskMapZoom()}
                        maxBounds={maxBounds}
                        minZoom={setInitialFloodRiskMapZoom()}
                        keyboard={false}
                        dragging={setDragging()}
                        doubleClickZoom={false}
                    >
                        <div className='fllod_risk_map_heading'>
                            <p> Hazard </p>
                        </div>

                        <TileLayer
                            attribution="Tiles &copy; Esri"
                            url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer/tile/{z}/{y}/{x}"
                        />
                        <div className="flood-legend-panel-desktop">
                            <FloodRiskLegend heading="Normalized Hazard" />
                        </div>
                        <GeoJSON data={india_boundary}
                            interactive={false}
                            style={
                                {
                                    fillColor: "white",
                                    color: "black",
                                    fillOpacity: "1",
                                    weight: "1"
                                }
                            }
                        />
                        <GeoJSON data={IndiaSubBasins.features}
                            style={(feature) => DistrictStyle(feature, "Hazard")}
                            onEachFeature={(feature, layer) => DistrictOnEachfeature(feature, layer, "Hazard")}
                        />
                    </MapContainer>
                </div>

                <div className='flood_risk_maps'>
                    <MapContainer
                        fullscreenControl={true}
                        center={[23, 84]}
                        style={{ width: '100%', height: "100%", backgroundColor: 'white', border: 'none', margin: 'auto' }}
                        zoom={setInitialFloodRiskMapZoom()}
                        maxBounds={maxBounds}
                        minZoom={setInitialFloodRiskMapZoom()}
                        keyboard={false}
                        dragging={setDragging()}
                        doubleClickZoom={false}
                    >
                        <div className='fllod_risk_map_heading'>
                            <p> Exposure </p>
                        </div>

                        <div className="flood-legend-panel-desktop">
                            <FloodRiskLegend heading="Normalized Exposure" />
                        </div>
                        <TileLayer
                            attribution="Tiles &copy; Esri"
                            url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer/tile/{z}/{y}/{x}"
                        />
                        <GeoJSON data={india_boundary}
                            interactive={false}
                            style={
                                {
                                    fillColor: "white",
                                    color: "black",
                                    fillOpacity: "1",
                                    weight: "1"
                                }
                            }
                        />
                        <GeoJSON data={IndiaSubBasins.features}
                            style={(feature) => DistrictStyle(feature, "Exposure")}
                            onEachFeature={(feature, layer) => DistrictOnEachfeature(feature, layer, "Exposure")}
                        />
                    </MapContainer>
                </div>

                <div className='flood_risk_maps'>
                    <MapContainer
                        fullscreenControl={true}
                        center={[23, 84]}
                        style={{ width: '100%', height: "100%", backgroundColor: 'white', border: 'none', margin: 'auto' }}
                        zoom={setInitialFloodRiskMapZoom()}
                        maxBounds={maxBounds}
                        minZoom={setInitialFloodRiskMapZoom()}
                        keyboard={false}
                        dragging={setDragging()}
                        doubleClickZoom={false}
                    >
                        <div className='fllod_risk_map_heading'>
                            <p> Risk </p>
                        </div>

                        <div className="flood-legend-panel-desktop">
                            <FloodRiskLegend heading="Normalized Risk" />
                        </div>
                        <TileLayer
                            attribution="Tiles &copy; Esri"
                            url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer/tile/{z}/{y}/{x}"
                        />
                        <GeoJSON data={india_boundary}
                            interactive={false}
                            style={
                                {
                                    fillColor: "white",
                                    color: "black",
                                    fillOpacity: "1",
                                    weight: "1"
                                }
                            }
                        />
                        <GeoJSON data={IndiaSubBasins.features}
                            style={(feature) => DistrictStyle(feature, "Risk")}
                            onEachFeature={(feature, layer) => DistrictOnEachfeature(feature, layer, "Risk")}
                        />
                    </MapContainer>
                </div>

            </div>
        </div>
    );
};

export default FloodRiskPage;
